//Descripci√≥n: Esta es la vista de resultados de b√∫squeda de recetas en la aplicaci√≥n Forkify.
// Esta vista se encarga de renderizar los resultados de b√∫squeda obtenidos del modelo,
// mostrando una lista de recetas que coinciden con la consulta del usuario.
// Utiliza la clase View para manejar la renderizaci√≥n de los resultados y la clase icons
// para mostrar iconos SVG en la interfaz de usuario.
//Repositorio:https://github.com/bmjimenez/proyectojs2
//Fecha: 2025-07-19

// Importando la clase View 
import View from './view.js';
//import icons from 'url:../../img/icons.svg';

// ResultsView hereda de la clase View para reutilizar m√©todos comunes de renderizaci√≥n
class ResultsView extends View {
  _parentElement = document.querySelector('.results');
  _errorMessage = 'No recipes found for your query. Please try again!';
  _message = '';

  // M√©todo para renderizar los resultados de b√∫squeda
  // Este m√©todo toma los datos de resultados de b√∫squeda y genera el HTML correspondiente
  // utilizando el m√©todo _generateMarkupPreview. Luego, limpia el contenido previo del elemento
  // padre y agrega el nuevo HTML al principio.

  _generateMarkup() {
    if (!this._data || this._data.length === 0) {
      return this.renderError(this._errorMessage)};
    return this._data.map(this._generateMarkupPreview).join('');
  }

  _generateMarkupPreview(result) {
    return `
      <li class="preview">
        <a class="preview__link" href="#${result.id}">
          <figure class="preview__fig">
            <img src="${result.image}" alt="${result.title}" />
          </figure>
          <div class="preview__data">
            <h4 class="preview__title">${result.title}</h4>
            <p class="preview__publisher">${result.publisher}</p>
            <div class="preview__user-generated"></div>
          </div>
        </a>
      </li>`;
  }
}// termina la clase ResultsView

// Exportando una instancia de ResultsView para que pueda ser utilizada en otras partes de la aplicaci√≥n
// Esto permite que la vista de resultados sea reutilizable y se pueda acceder a sus m√©todos y
// propiedades desde otras partes del c√≥digo, como el controlador principal de la aplicaci√≥n.
// Al exportar una instancia, se asegura que siempre se utilice la misma instancia de ResultsView
// en toda la aplicaci√≥n, lo que facilita el manejo del estado y la renderizaci√≥n de resultados.
// Esto es √∫til para mantener la consistencia en la interfaz de usuario y evitar problemas de
// duplicaci√≥n de vistas o resultados.
// Adem√°s, al exportar una instancia, se puede acceder a los m√©todos de la clase ResultsView
// directamente desde el controlador, lo que simplifica la l√≥gica de renderizaci√≥n de resultados
// y mejora la legibilidad del c√≥digo.
export default new ResultsView();