import View from './view.js';
import icons from 'url:../../img/icons.svg';

// Clase PaginationView que maneja la paginaci√≥n de los resultados de b√∫squeda
// Esta clase hereda de la clase View para reutilizar m√©todos comunes de renderizaci√≥n
// y manejo de errores. Se encarga de renderizar los botones de paginaci√≥n
// y manejar los eventos de clic en los botones de paginaci√≥n.
class PaginationView extends View {
  _parentElement = document.querySelector('.pagination');

  // M√©todo para renderizar los botones de paginaci√≥n
  // Este m√©todo genera el HTML necesario para los botones de paginaci√≥n
  // y lo inserta en el contenedor de paginaci√≥n.
  addHandlerClick(handler) {
    this._parentElement.addEventListener('click', function (e) {
      const btn = e.target.closest('.btn--inline');
      if (!btn) return;

      const goToPage = +btn.dataset.goto;
      console.log('GotoPage', `${goToPage}`);
      handler(goToPage);
    });
  }
  // M√©todo para generar el HTML de los botones de paginaci√≥n
  // Este m√©todo toma los datos de paginaci√≥n y genera el HTML correspondiente
  _generateMarkup() {
    const curPage = this._data.page;
    const numPages = Math.ceil(this._data.results.length / this._data.resultsPerPage);
    //console.log('Total de paginas', `${numPages}`);


    // P√°gina 1, y hay m√°s
    if (curPage === 1 && numPages > 1) {
      return `
        <button data-goto="${curPage + 1}" class="btn--inline pagination__btn--next">
          <span>Page ${curPage + 1}</span>
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-right"></use>
          </svg>
        </button>
      `;
    }

    // √öltima p√°gina
    if (curPage === numPages && numPages > 1) {
      return `
        <button data-goto="${curPage - 1}" class="btn--inline pagination__btn--prev">
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-left"></use>
          </svg>
          <span>Page ${curPage - 1}</span>
        </button>
      `;
    }

    // P√°gina intermedia
    if (curPage < numPages) {
      return `
        <button data-goto="${curPage - 1}" class="btn--inline pagination__btn--prev">
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-left"></use>
          </svg>
          <span>Page ${curPage - 1}</span>
        </button>
        <button data-goto="${curPage + 1}" class="btn--inline pagination__btn--next">
          <span>Page ${curPage + 1}</span>
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-right"></use>
          </svg>
        </button>
      `;
    }

    // Solo una p√°gina
    return '';
  }
}// termina la clase PaginationView

// Exportando una instancia de PaginationView para que pueda ser utilizada en otras partes de la aplicaci√≥n
// Esto permite que la vista de paginaci√≥n sea reutilizable y se pueda acceder a sus m√©todos y
// propiedades desde otras partes del c√≥digo, como el controlador principal de la aplicaci√≥n.
export default new PaginationView();
