//manejar la l√≥gica de la aplicaci√≥n, incluyendo la obtenci√≥n de recetas y su renderizado.
//Implementando el patr√≥n de dise√±o MVC (Modelo-Vista-Controlador) para separar la l√≥gica de negocio
//de la l√≥gica de presentaci√≥n y facilitar el mantenimiento y la escalabilidad de la aplicaci√≥n.
