// Importando las dependencias necesarias
// Importando el modelo y las constantes de configuraci√≥n
import * as model from './model.js';
import { API_URL, TIMEOUT_SEC } from './config.js'; // Importando las constantes  URL y timeout
import { Fraction_function } from './helpers.js'; // Importando la clase Fraction_function
import { loadSearchResults,getSearchResultsPage}  from './model.js';
import  icons  from 'url:../img/icons.svg'; // Importando los iconos SVG

// Importando las vistas necesarias
import recipeView from './views/RecipeView.js';
import searchView from './views/searchView.js';
import resultsView from './views/resultView.js';
import paginationView from './views/paginationView.js'


// Seleccionando el elemento del DOM donde se renderizar√° la receta
const recipeContainer = document.querySelector('.recipe');


// Definiendo el controlador de recetas
// Este controlador se encarga de manejar la l√≥gica de las recetas, incluyendo la carga y renderizado de las mismas
// Utiliza la funci√≥n loadRecipe del modelo para obtener la receta y luego
// renderiza la receta utilizando la vista recipeView
// Tambi√©n maneja errores y muestra mensajes de error si ocurre alg√∫n problema al cargar la receta
// La funci√≥n controlRecipes es as√≠ncrona para manejar operaciones que pueden tardar, como
// la carga de datos desde una API o base de datos.
const controlRecipes = async function() {
  try {
    // Obtener el ID de la receta desde el hash de la URL
    const id = window.location.hash.slice(1); 
    // Si no hay ID, salir de la funci√≥n
    if (!id) {return;} 
    // Renderizar spinner mientras carga la receta
    recipeView.renderSpinner();
    // Cargar la receta utilizando el modelo
    await model.loadRecipe(id);
    // Renderizar la receta
    recipeView.render(model.state.recipe);
    // Mensaje de √©xito al cargar la receta
   // recipeView.renderMessage('Receta cargada correctamente!'); 
    // temporal Mostrar los datos de la receta en la consola para depuraci√≥n
    console.log('Receta cargada:', model.state.recipe);
  } catch (err) {
    //Se lanza un error si ocurre un problema al cargar la receta
    console.error(`Error al cargar la receta: ${err}`);
    // Renderizar mensaje de error en la vista recipeView
    // Esto permite mostrar un mensaje de error al usuario en la interfaz de usuario
    // si ocurre un problema al cargar la receta, como un error de red o un ID
    recipeView.renderError(err.message);
    throw err; // Lanzar el error para que sea manejado por el controlador
  }
}// termina funci√≥n controlRecipes


// Definiendo el controlador de b√∫squeda de resultados
// Este controlador se encarga de manejar la l√≥gica de b√∫squeda de recetas, incluyendo la carga y
// renderizado de los resultados de b√∫squeda. Utiliza la funci√≥n loadSearchResults del modelo
// para obtener los resultados de b√∫squeda y luego renderiza los resultados utilizando la vista resultsView.
// Tambi√©n maneja la paginaci√≥n de los resultados y muestra mensajes de error si ocurre alg√∫n problema al cargar los resultados de b√∫squeda.
// La funci√≥n controlSearchResults es as√≠ncrona para manejar operaciones que pueden tardar, como
// la carga de datos desde una API o base de datos
// y utiliza la vista searchView para obtener la consulta de b√∫squeda del usuario.
const controlSearchResults = async function () {
  try {
    const query = searchView.getQuery();

    if (!query) return;
    // Renderizar spinner
    resultsView.renderSpinner();
    // Cargar los resultados de b√∫squeda utilizando el modelo
    await model.loadSearchResults(query);
    // Renderizar los resultados de b√∫squeda
    resultsView.render(getSearchResultsPage());
    // Renderizar la paginaci√≥n de resultados
    paginationView.render(model.state.search);

    console.log(model.state.search.results); // Muestra los resultados en consola
  } catch (err) {
    console.error('‚ùå Error en controlSearchResults:', err);
  }
};// termina funci√≥n controlSearchResults


// Definiendo la funcion de controlador de paginaci√≥n
// Este controlador se encarga de manejar la l√≥gica de paginaci√≥n de los resultados de b√∫squeda
// Utiliza la vista paginationView para renderizar los botones de paginaci√≥n y
// la funci√≥n getSearchResultsPage del modelo para obtener los resultados de b√∫squeda de la p√°gina actual
// La funci√≥n controlPagination es as√≠ncrona para manejar operaciones que pueden tardar, como
// la carga de datos desde una API o base de datos.
// Tambi√©n maneja la l√≥gica de navegaci√≥n entre p√°ginas de resultados de b√∫squeda.
const controlPagination = function (goToPage) {
  resultsView.render(model.getSearchResultsPage(goToPage));
  paginationView.render(model.state.search);
};


// Inicializando el controlador
// Esta funci√≥n se encarga de inicializar el controlador, a√±adiendo los manejadores de eventos necesarios
// para renderizar la receta cuando se carga la p√°gina o se cambia el hash de la URL.
// Utiliza la vista recipeView para a√±adir el manejador de eventos y renderizar la receta
// Tambi√©n a√±ade los manejadores de eventos para la b√∫squeda y paginaci√≥n utilizando las vistas
// searchView y paginationView respectivamente.
// La funci√≥n init se llama al final para iniciar el controlador. 
const init = function() {
  recipeView.addHandlerRender(controlRecipes); 
  searchView.addHandlerSearch(controlSearchResults);
  paginationView.addHandlerClick(controlPagination);
  console.log('Controlador inicializado');

}
init(); // Llamada al controlador inicial para iniciar la aplicaci√≥n


