// Importando la URL de la API y la funci√≥n getJSON para realizar peticiones HTTP
import { API_URL,RES_PER_PAGE } from './config.js'; // Importando la URL de la API   
import { getJSON } from './helpers.js';


//Declaracion de objeto state con elementos inicializados
// Este objeto state contiene la receta actual y los resultados de b√∫squeda.
// Se utiliza para almacenar el estado de la aplicaci√≥n y facilitar la gesti√≥n de datos.
// La propiedad recipe almacena la receta actual, mientras que search contiene los resultados de b√∫squeda,
// la consulta de b√∫squeda, la p√°gina actual y el n√∫mero de resultados por p√°gina.
// Este objeto se utiliza en el controlador para acceder a los datos de la aplicaci√≥n y renderizar
// las vistas correspondientes.
export const state = {
    recipe: {},
    search: {
        query: '',
        results: [],
        page: 1,
        resultsPerPage: RES_PER_PAGE,
    },
};

//Funci√≥n as√≠ncrona loadRecipe que recibe id como par√°metro
// Esta funci√≥n se encarga de cargar una receta espec√≠fica desde la API
// y actualizar el objeto state con los datos de la receta.
// Si ocurre un error, se captura y se muestra en la consola.
export async function loadRecipe(id) {
    try {
        // Llamada a la funci√≥n getJSON para obtener los datos de la receta
        const data = await getJSON(`${API_URL}${id}`);
        console.log('Datos de la receta:', data);
        // Validar response structure recibida de la API
        // Si la estructura de datos no es v√°lida, se lanza un error
        if (!data?.data?.recipe) {
            throw new Error('Estructura de datos invalida');
        }
        // Desestructuraci√≥n del objeto recipe y guardado en state
        // Aqu√≠ se extraen los datos de la receta del objeto data y se crea un nuevo
        // objeto recipe con las propiedades necesarias.
        // Se asegura de que la propiedad ingredients sea un array, incluso si no hay ingredientes.
        // Esto es importante para evitar errores al renderizar la receta en la vista.  
        const { recipe } = data.data;
        const recipeObj = {
            id: recipe.id,
            title: recipe.title,
            publisher: recipe.publisher,
            sourceUrl: recipe.source_url,
            image: recipe.image_url,
            servings: recipe.servings,
            cookTime: recipe.cooking_time,
            ingredients: Array.isArray(recipe.ingredients) ? recipe.ingredients : [],
        };

        //Guardar en en objeto state y mostrar en consola
        // Aqu√≠ se actualiza el objeto state con la receta cargada,
        // lo que permite que otras partes de la aplicaci√≥n accedan a los datos de la receta
        state.recipe = recipeObj;
        console.log('Objeto Recipe cargado:', state.recipe);

    } catch (err) {
        // Lanzar el error para que sea manejado por el controlador
        throw err;
        // Aqu√≠ se captura cualquier error que ocurra durante la carga de la receta
        // y se lanza para que pueda ser manejado por el controlador.
        // Esto es √∫til para manejar errores de red, errores de la API o errores de validaci√≥n de datos.
        // Tambi√©n se puede mostrar un mensaje de error al usuario en la vista.
        // En este caso, se lanza el error para que sea manejado por el controlador
        // y se pueda mostrar un mensaje de error adecuado en la interfaz de usuario.
        // Se puede utilizar console.log para mostrar el error en la consola durante el desarrollo.
        console.log(`Error al cargar receta: ${err}`);
    }
}// termina la funci√≥n loadRecipe

// Funci√≥n as√≠ncrona loadSearchResults que recibe query como par√°metro
// Esta funci√≥n se encarga de cargar los resultados de b√∫squeda desde la API
// y actualizar el objeto state con los resultados de b√∫squeda.
// Si ocurre un error, se captura y se muestra en la consola.
export async function loadSearchResults(query) {
    try {
        const data = await getJSON(`${API_URL}?search=${query}`);
        console.log('Resultados de la b√∫squeda:', data);

        // Validar estructura de datos recibida de la API
        // Si la estructura de datos no es v√°lida, se lanza un error
        // Aqu√≠ se verifica que la propiedad recipes exista en el objeto data.data
        // y que sea un array. Si no es as√≠, se lanza un error.
        // Esto es importante para asegurarse de que los datos recibidos sean v√°lidos
        // y se puedan procesar correctamente en la aplicaci√≥n.
        if (!data?.data?.recipes || !Array.isArray(data.data.recipes)) {
            throw new Error('Estructura de datos inv√°lida');
        }
        // Guardar query en el estado
        state.search.query = query;
        // Transformar los resultados y guardarlos en el objeto state
        // Aqu√≠ se mapea el array de recetas recibido de la API y se crea un nuevo
        // array de objetos con las propiedades necesarias para mostrar en la vista.
        // Se asegura de que cada objeto tenga las propiedades id, title, publisher e image.
        // Esto es importante para que la vista pueda renderizar correctamente los resultados de b√∫squeda.
        // Se utiliza el m√©todo map para transformar los datos y crear un nuevo array de objetos.
        // Luego, se guarda este array en la propiedad results del objeto state.search.
        // Esto permite que otras partes de la aplicaci√≥n accedan a los resultados de b√∫squeda
        state.search.results = data.data.recipes.map(rec => {
            return {
                id: rec.id,
                title: rec.title,
                publisher: rec.publisher,
                image: rec.image_url,
            };
        });

        console.log('Resultados de busqueda guardados:', `${state.search.results}`);
    } catch (err) {
        console.log(`${err} üí•üí•üí•üí•`);
        throw err; // Lanzar el error para que sea manejado por el controlador  
    }
}// termina la funci√≥n loadSearchResults

// Funci√≥n para obtener una p√°gina de resultados de b√∫squeda
// Esta funci√≥n toma un n√∫mero de p√°gina como argumento y devuelve los resultados de b√∫squeda
// correspondientes a esa p√°gina. Si no se proporciona un n√∫mero de p√°gina, se utiliza el valor
// actual de la p√°gina en el objeto state.search.page.
// Se calcula el √≠ndice de inicio y fin de los resultados para esa p√°gina y se devuelve un
// array con los resultados correspondientes.
// Esta funci√≥n es √∫til para implementar la paginaci√≥n de resultados de b√∫squeda en la aplicaci√≥n.
// Permite obtener los resultados de b√∫squeda de una p√°gina espec√≠fica y facilita la navegaci√≥n
// entre p√°ginas de resultados.
export const getSearchResultsPage = function (page = state.search.page) {
    state.search.page = page;

    const start = (page - 1) * state.search.resultsPerPage;
    const end = page * state.search.resultsPerPage;

    return state.search.results.slice(start, end);
};