//Importa iconos
import icons from 'url:../../img/icons.svg'; 
//import View from './view.js';

// Clase View que maneja la renderizaci√≥n de datos en el DOM
// Esta clase se encarga de renderizar los datos en el DOM, mostrar errores y spinner
// y manejar eventos relacionados con la vista.
// La clase View es una clase base (padre) que puede ser extendida por otras vistas espec√≠ficas,
// como RecipeView o SearchView, para reutilizar m√©todos comunes de renderizaci√≥n y manejo de errores.
// Esta clase proporciona m√©todos para renderizar datos, mostrar errores y mensajes,
// y renderizar un spinner mientras se cargan los datos.
// Tambi√©n proporciona un m√©todo para limpiar el contenido previo antes de renderizar nuevos datos.
// La clase View es una parte fundamental de la arquitectura de la aplicaci√≥n, ya que
// permite separar la l√≥gica de negocio de la l√≥gica de presentaci√≥n, facilitando el mantenimiento
// y la escalabilidad de la aplicaci√≥n.
// Esta clase es utilizada por otras vistas para renderizar datos en el DOM y manejar eventos relacionados
// con la vista, como la b√∫squeda de recetas o la visualizaci√≥n de recetas espec√≠ficas.
export default class View {
  _data;

  // M√©todo para renderizar los datos en el DOM
  // Este m√©todo toma los datos y genera el HTML correspondiente utilizando el m√©todo _generateMarkup.
  // Luego, limpia el contenido previo del elemento padre y agrega el nuevo HTML al principio.
  // Si los datos son inv√°lidos o no existen, se renderiza un mensaje de error
  // utilizando el m√©todo renderError.
  // Si los datos son v√°lidos, se renderiza el HTML generado por _generateMarkup
  // y se limpia el contenido previo del elemento padre antes de agregar el nuevo HTML.
  // Este m√©todo es utilizado por las vistas para renderizar datos en el DOM y actualizar la
  // interfaz de usuario con la informaci√≥n m√°s reciente.
  render(data) {
    if (!data || (Array.isArray(data) && data.length === 0)) return this.renderError();

    this._data = data;
    const markup = this._generateMarkup();
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  // M√©todo para limpiar el contenido previo del elemento padre
  _clear() {
    this._parentElement.innerHTML = '';
  }

  // M√©todo para renderizar un spinner mientras se cargan los datos
  // Este m√©todo genera un HTML para un spinner de carga y lo inserta en el elemento
  // padre antes de renderizar los datos. Se utiliza para indicar al usuario que los datos
  // est√°n siendo cargados y que debe esperar.
  // El spinner se muestra mientras se realizan operaciones as√≠ncronas, como la carga de
  // datos desde una API o base de datos.
  renderSpinner() {
    const markup = `
      <div class="spinner">
        <svg>
          <use href="${icons}#icon-loader"></use>
        </svg>
      </div>`;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  // M√©todo para renderizar un mensaje de error
  // Este m√©todo genera un HTML para un mensaje de error y lo inserta en el elemento
  // padre. 
  renderError(message = this._errorMessage) {
    const markup = `
      <div class="error">
        <div>
          <svg>
            <use href="${icons}#icon-alert-triangle"></use>
          </svg>
        </div>
        <p>${message}</p>
      </div>`;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  // M√©todo para renderizar un mensaje de √©xito
  // Este m√©todo genera un HTML para un mensaje de √©xito y lo inserta en el elemento
  // padre. 
  renderMessage(message = this._message) {
    const markup = `
      <div class="message">
        <div>
          <svg>
            <use href="${icons}#icon-smile"></use>
          </svg>
        </div>
        <p>${message}</p>
      </div>`;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }
}// termina la clase View
