// Esta clase SearchView se encarga de manejar la vista de b√∫squeda en la aplicaci√≥n Forkify.
// Permite obtener la consulta de b√∫squeda del usuario y manejar el evento de b√∫squeda.
// Utiliza el patr√≥n de dise√±o de vista para separar la l√≥gica de la interfaz de usuario de
// la l√≥gica de negocio, facilitando el mantenimiento y la escalabilidad de la aplicaci√≥n.
class SearchView {
  _parentEl = document.querySelector('.search');

  // M√©todo para obtener la consulta de b√∫squeda del usuario
  // Este m√©todo busca el valor del campo de entrada de b√∫squeda y lo devuelve.
  getQuery() {
    const query = this._parentEl.querySelector('.search__field').value;
    this._clearInput();
    return query;
  }

  // M√©todo para limpiar el campo de entrada de b√∫squeda
  _clearInput() {
    this._parentEl.querySelector('.search__field').value = '';
  }

  // M√©todo para renderizar un mensaje de error en la vista de b√∫squeda
  addHandlerSearch(handler) {
    this._parentEl.addEventListener('submit', function (e) {
      e.preventDefault();
      handler();
    });
  }
}// termina la clase searchView

// Exportando una instancia de SearchView para que pueda ser utilizada en otras partes de la aplicaci√≥n
export default new SearchView();